import debounce from "lodash/debounce";
export default {
    data() {
        return {
            searchData: [],
            fetching: false,
            typingData: ""
        }
    },
    methods: {
        getTeamSearchResults: debounce(function(name) {
            if (!name.length) {
                this.searchData = [];
                return;
            }
            this.fetchingPlayer = true;
            this.$axios(process.env.baseUrl + `api/teams?name[$search]=${name}`, {
                method: "get",
                    headers: {
                        "content-type": "application/json"
                    }
            })
                .then(function(response) {
                    return response.data;
                })
                .then(response => {
                    this.searchData = [];
                    response.data.forEach(item => this.searchData.push(item));
                })
                .catch(error => {
                    this.searchData = [];
                    throw error;
                })
                .finally(() => {
                    this.fetching = false;
                });
            }, 500),
        onFocus(e) {
            e.stopPropagation();
        },
        getTeamImgUrl(img) {
            return process.env.baseUrl + `api/uploads/teams/${img}`;
        },
        selectTeam(option) {
            this.$emit('selected', option._id)
        }
    }
}
